<template>
    <div class="our-blog">
        <h1>{{ pageName }}</h1>
        <p>{{ pageDescription }}</p>
        <div class="container">
            <div class="row">
                <div class="col-md-8">
                    <div class="posts-area">
                        <BlogPosts
                            v-for="post in posts"
                            v-bind:key="post.id"
                            v-bind:views="post.views"
                            v-bind:title="post.title"
                            v-bind:date="post.date"
                            v-bind:image="post.image"
                            v-bind:content="post.content"
                            v-bind:author="post.author"
                            v-bind:category="post.category"
                        />
                    </div>
                </div>
                <div class="col-md-4">
                    <div class="sidebar">
                        <BlogSidebar />
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import BlogPosts from "@/components/Blog/BlogPosts.vue";
import BlogSidebar from "@/components/Blog/BlogSidebar.vue";
import jsonPosts from "../json/blogPosts.json";

export default {
    data: function () {
        return {
            pageName: "Blog",
            pageDescription: "This Is Blog Page ",
            posts: jsonPosts,
        };
    },

    name: "blog",
    components: {
        BlogPosts,
        BlogSidebar,
    },
};
</script>
