<template>
    <div class="container">
        <div
            class="grad-box"
            :style="{
                backgroundImage:
                    'linear-gradient(' +
                    direction +
                    ' , ' +
                    colorOne +
                    ' , ' +
                    colorTwo +
                    ')',
            }"
        >
            Gradient Preview
        </div>
        <form action="">
            <div class="form-group">
                <span>Sélectionné : {{ selected }}</span>
                <select v-model="direction">
                    <option disabled value="">Chose</option>
                    <option>to right</option>
                    <option>to left</option>
                    <option>to bottom</option>
                    <option>to top</option>
                </select>
                
            </div>
            <span class="separator">|</span>
            <div class="form-group">
                <label for="">color stop one</label>
                <input type="text" v-model="colorOne" />
            </div>
            <span class="separator">|</span>
            <div class="form-group">
                <label for="">color stop two</label>
                <input type="text" v-model="colorTwo" />
            </div>
        </form>
        <div class="result">
            <span>Css Code:</span> background-image: linear-gradient({{
                direction
            }}, {{ colorOne }}, {{ colorTwo }});
        </div>
        <div class="syntax">
            <span>Syntax:</span> background-image: linear-gradient(direction,
            color-stop1, color-stop2, ...);
        </div>
    </div>
</template>
<script>
export default {
    data: function () {
        return {
            direction: ["to right", "to left", "to bottom", "to top"],
            colorOne: "#ff0000",
            colorTwo: "#ffff00",
        };
    },
};
</script>
