<template>
    <div class="contact-our">
        <div class="container">
            <form action="">
                <div class="left">
                    <input
                        type="text"
                        placeholder="Your Name"
                        v-model="username"
                    />
                    <input
                        type="text"
                        placeholder="Your Phone"
                        v-model="phone"
                    />
                    <input
                        type="email"
                        placeholder="Your Email"
                        v-model="email"
                    />
                    <input type="text" placeholder="Subject" name="subject" />
                </div>
                <div class="right">
                    <textarea
                        name="message"
                        placeholder="Your Message"
                    ></textarea>
                    <input
                        :disabled="!username || !phone || !email"
                        value="Send"
                    />
                </div>
            </form>
        </div>
    </div>
</template>
<script>
export default {
    data: function () {
        return {
            username: null,
            phone: null,
            email: null,
        };
    },
};
</script>
